<template>
	<!-- 我的课程三级页面 -->
   <div class="videoPSec">
	   <div class="vbg">
	   </div>
	   <div class="vback" @click="back()">
	   	<img src="../../../../assets/img/vback.png" >返回
	   </div>
	   <div class="lslid" v-show="false">
	   	  <div class="lslid1">
	   	  	<img src="../../../../assets/img/sys.png" >
			<div class="text">
				目录
			</div>
	   	  </div>
		  <div class="lslid2">
		  	<img src="../../../../assets/img/folder.png" >
			<div class="text">
				资料
			</div>
		  </div>
	   </div>
	   <div class="rslid">
		<!--   <div class="rstitle2 process">
		   	完成进度:<input type="text" v-model="process" class="notename"/>
		   </div> -->
		   <div class="rstitle">
		   	<img src="../../../../assets/img/note.png" >
			笔记
		   </div>
		   <div class="rstitle2">
		   	笔记名称:<input type="text" v-model="notename" class="notename"/>
		   </div>
		   <div class="noteContent">
		   	<el-input type="textarea" class="contentarea" v-model="esc" rows="36"></el-input>
		   </div>
		   <div class="vbtns">
		   	  <el-button type="primary" @click="savenote">保存</el-button>
			  <!-- <el-button type="primary" plain>取消</el-button> -->
		   </div>
	   </div>   
	   <videoP></videoP>
   </div>
</template>

<script>
import videoP from '../../../../components/secCommon/videoP.vue'
import {savenote} from '@/api/test/user.js'
export default {
	name:'videoPSec',
	data(){
		return {
			notename:'',
			esc:'',
			process:''
		}
	},
	mounted() {
	},
	components:{
		videoP
	},
	methods:{
		back(){
			this.$router.go(-1)
		},
		savenote(){
			console.log("保存笔记")
			savenote({className:this.notename,user:1,name:"张三",esc:this.esc}).then(res=>{
				
			})
		}
	}
}
</script>


<style scoped>
	@import url("../../../../assets/css/videoPSec.css");
</style>



