<template>
   <el-row class="videoP">
	   <el-row>
		   <div class="vtitle">
		   	第{{nclass.index+1}}章 {{nclass.name}}
		   </div>
	   </el-row>
	   <el-row>
		   <video :src="movie" class="video-player vjs-custom-skin" controls="controls">
		   您的浏览器不支持 video 标签。
		   </video>
	   </el-row>
	   <el-row class="leftml">
		   <el-row v-for="(item,index) in nclass.children" :key="index" class="leftm" >
			   <div @click="video(item)">
			   	 {{item.name}}
			   </div>
		   </el-row>
	   </el-row>
   </el-row>
</template>

<script>
	import url from '../../../static/config/video.json'
export default {
	name:'videoP',
	data(){
		return {
			nclass:[],
			movie:'',
			  playerOptions: {
			            playbackRates: [0.5, 1.0, 1.5, 2.0], // 可选的播放速度
			            autoplay: false, // 如果为true,浏览器准备好时开始回放。
			            muted: false, // 默认情况下将会消除任何音频。
			            loop: false, // 是否视频一结束就重新开始。
			            preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
			            language: 'zh-CN',
			            aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
			            fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
			            sources: [{
			              type: "video/mp4", // 类型
			              src: '' // url地址
			            }],
			            poster: '', // 封面地址
			            notSupportedMessage: '此视频暂无法播放，请稍后再试', // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
			           
			}
		}
	},
	mounted() {
		let nclass=localStorage.getItem('nowClass')
		this.nclass=JSON.parse(nclass)
		this.movie=url.url+this.nclass.children[0].video[0].filePath
	},
	components:{
	},
	methods:{
		video(item){
			this.movie=url.url+item.video[0].filePath
		}
	}
}
</script>


<style scoped>
@import '../../assets/css/videoP.css';
</style>



